@import "variables";
//@import 'extra-variables';

// nulling styles ===============================================================================================================================

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

// fonts ===================================================================================================================

@import url(https://fonts.googleapis.com/css?family=Nunito:regular,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik:regular,500,700&display=swap);

// Global styles ===================================================================================================================

* {
  font-family: $fontFamily;
  font-size: $fontSize;
  color: $mainTextColor;
}

.container {
  max-width: $maxContWidth + px;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
}

.content {
  flex: 1 1 auto;
}

// common ===================================================================================================================

.text {
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.05em;
  color: $secondaryTextColor;
}

.title {
  font-family: "Nunito";
  font-weight: 700;
  font-size: 32px;
  line-height: 44px;
  text-align: center;
  letter-spacing: 0.04em;
  position: relative;
  &::before{
    content: '';
    position: absolute;
    height: 10px;
  }
}
